@tailwind base;
@tailwind components;
@tailwind utilities;

* {
    font-size: 1rem;
    margin: 0;
    padding: 0;
    --headerBackground: white;
    --bodyBackground: rgb(238, 238, 238);
    --cardBackground: white;
    --textColor: black;
    --theme: rgb(0, 212, 0);
    text-decoration: none;
    color: var(--textColor);
    scrollbar-width: thin;
}
html {
    scroll-behavior: smooth;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    top: 0;
    width: 100%;
    color: var(--textColor);
    background-color: var(--headerBackground);
    z-index: 10;
}
.headerOffset {
    width: 100%;
    height: 4.7rem;
}
.logo {
    font-size: 2rem;
    font-family: monospace;
    font-weight: bold;
}
.navItems {
    display: flex;
    list-style-type: none;
    margin: 0 1rem;
}
.navItems * {
    padding: 0.5rem 1.5rem;
    white-space: nowrap;
    text-decoration: none;
    color: var(--textColor);
}
.cart {
    background-color: var(--theme);
    font-family: monospace;
    font-weight: 500;
    color: white;
    border-radius: 5rem;
}
.cart:hover {
    background-color: green;
}

.body {
    padding-top: 1rem;
    background-color: var(--bodyBackground);
}
.filterBar {
    font-family: monospace;
    display: flex;
    justify-content: space-between;
    margin: auto;
    /* min-width: 27rem; */
    /* max-width: 80rem; */
    width: 80%;
    white-space: nowrap;
    color: black;
    overflow: hidden;
    transition: 1.5s;
}
@media screen and (max-width: 90rem) {
    .filterBar {
        width: 90%;
        flex-wrap: wrap;
    }
}
.search {
    width: 15rem;
    padding: 1px;
    z-index: 5;
}
.overlay {
    position: fixed;
    top: 5rem;
    right: 0;
    width: 100%;
    height: 100%;
    backdrop-filter: blur(3px);
    z-index: 3;
}
.searchInput {
    position: absolute;
    border-radius: 1rem;
    border: 1px solid white;
    padding: 0 1rem;
    width: 15rem;
    transition: 1s;
    z-index: 5;
}
.searchInput:hover {
    border: 1px solid gray;
}

.searchResult {
    position: absolute;
    margin: 2rem auto;
    border-radius: 1rem;
    max-height: 40rem;
    width: 80%;
    z-index: 2;
    background-color: white;
    /* scrollbar-width: none; */
    overflow-y: scroll;
    overflow-x: hidden;
    z-index: 5;
    transition: 1s;
    border: 1px solid var(--theme);
}
.searchItem {
    padding: 2px 1rem;
    transition: 0s;
}
.searchItem:hover {
    background-color: var(--theme);
    color: white;
}
.filterBar button {
    color: rgb(49, 49, 49);
    padding: 2px 1rem 2px 1rem;
    margin: 1px;
    border-radius: 1rem;
    border: 1px solid rgb(204, 204, 204);
}
.filterBar button:hover {
    background-color: rgb(206, 206, 206);
    transition: 250ms;
}

/* .filterBar button:focus {
    border: 1px solid rgb(206, 206, 206);
    background-color: rgb(0, 212, 0);
    color: white;
} */

.foodContainer {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.foodCard {
    background-color: var(--cardBackground);
    /* background-image: linear-gradient(to bottom, white, var(--bodyBackground)); */
    color: var(--textColor);
    min-width: 15rem;
    width: 15rem;
    height: 21rem;
    margin: 1.5rem;
    border-radius: 7px;
    overflow: hidden;
}
.fadeUpAnimation {
    animation: fadeUp 0.5s linear;
}
@keyframes fadeUp {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
.loading {
    text-align: center;
    margin-top: 50%;
}
.foodCard:hover {
    /* scale: 1.1; */
    translate: -0.25rem -0.25rem;
    box-shadow: 2rem 2rem 2rem gray;
    transition: 0.3s;
    /* .storeImg {
        scale: 1.05;
        transition: 0.3s;
    } */
}
.foodCard:not(:hover) {
    transition: 0.2s;
}

.storeImg {
    width: 15rem;
    height: 10rem;
    margin-bottom: 0.5rem;
    object-fit: cover;
    border-radius: 7px;
}

.storeName {
    padding-left: 1rem;
    margin-top: 1px;
    font-weight: 600;
    margin-bottom: 0.5rem;
}
.foodItems {
    margin-top: 1px;
    padding-left: 1rem;
    color: gray;
}
.storeRating {
    color: rgb(189, 189, 189);
    padding-left: 1rem;
    display: flex;
    margin-top: 1px;
}
.storeRating div {
    padding-right: 0.5rem;
    white-space: nowrap;
}
.deliveryTime {
    padding-left: 1rem;
    margin-top: 1px;
}
.veg,
.nonVeg {
    margin: 1px 1rem;
    padding: 0 0.5rem;
    width: fit-content;
    text-align: center;
    white-space: nowrap;
    border-radius: 1rem;
}
.veg {
    border: 1px solid green;
    color: green;
}
.nonVeg {
    border: 1px solid red;
    color: red;
}
.pageNavigation {
    width: fit-content;
    margin: auto;
    display: flex;
}
.pageNum {
    background-color: rgb(209, 209, 209);
    text-align: center;
    height: 2.5rem;
    width: 2.5rem;
    margin: 0.5rem;
    line-height: 2.5rem;
    border-radius: 40rem;
    transition: 1s;
    cursor: pointer;
}
.restaurant {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: var(--bodyBackground);
}
.restaurantInfo {
    width: 20rem;
    margin: auto;
    transition: 1s;
}
.restaurantImage {
    width: 40rem;
    height: 40rem;
    object-fit: cover;
    overflow: hidden;
    border-radius: 1rem;
    opacity: 1;
    transition: 1.5s;
}

.restaurantInfo .name {
    color: green;
    font-size: 2rem;
    font-family: monospace;
    font-weight: bold;
}
.aboutUs {
    background-color: var(--bodyBackground);
    margin: auto;
    padding: 0 5rem;
    font-family: monospace;
    font-size: 1.3rem;
}

.Footer {
    height: 20rem;
    background-color: black;
    color: gray;
}

.contactInfo {
    padding: 1rem;
    margin: auto;
    max-width: 50vw;
    display: flex;
    justify-content: space-evenly;
}
.contactInfo a {
    color: gray;
    text-decoration: none;
}

@media screen and (max-width: 70rem) {
    .restaurantImage {
        width: 100%;
    }
    .restaurantInfo {
        padding-top: 3rem;
    }
}
